import React, {Component} from 'react';
import {
	Box, 
	Button,
	Heading,
	Paragraph,
	Text
} from 'grommet';

class ClassView extends Component {
	constructor(props) {
		super(props);
		this.classInfo = this.props.classInfo;
	}
	render ( ) {
		return (
			<Box
				flex
				direction='column'
				justify='start'
				align='start'
				pad='5%'
				animation={{
					type: 'fadeIn',
					delay: 50,
					duration: 750,
					size: 'large'
				}}
			>
				<Box direction='row' align='center' justify='between'>
					<Heading level='1'>{this.classInfo.className}</Heading>
					<Heading level='2'>{this.classInfo.degreeName+' '+this.classInfo.classNumber}</Heading>
				</Box>
				<Paragraph>
					{this.classInfo.summary}
				</Paragraph>
				<Button
					href='/Main'
					hoverIndicator
					label='Reload this page ... '
					alignSelf='center'
					margin={{
						top:  '10%'
					}}
				>
				</Button>
			</Box>
		);
	}
}

export default ClassView;